@font-face {
  font-family: "poppins";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/poppins-regular-webfont.woff2") format("woff2"), url("../fonts/poppins-regular-webfont.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "poppins";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/poppins-medium-webfont.woff2") format("woff2"), url("../fonts/poppins-medium-webfont.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "poppins";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/poppins-bold-webfont.woff2") format("woff2"), url("../fonts/poppins-bold-webfont.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "cormorantgaramond";
  font-style: italic;
  font-weight: 400;
  src: url("../fonts/cormorantgaramond-italic-webfont.woff2") format("woff2"), url("../fonts/cormorantgaramond-italic-webfont.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "cormorantgaramond";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/cormorantgaramond-semibold-webfont.woff2") format("woff2"), url("../fonts/cormorantgaramond-semibold-webfont.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "cormorantgaramond";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/cormorantgaramond-bold-webfont.woff2") format("woff2"), url("../fonts/cormorantgaramond-bold-webfont.woff") format("woff");
  font-display: swap;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "poppins", "Arial", sans-serif;
  font-size: 16px;
  line-height: 130%;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  min-width: 320px;
  background-color: #ffffff;
  color: #1d1613;
}

.list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.button {
  border: none;
  outline: none;
}

.link {
  display: block;
  position: relative;
  text-decoration: none;
  color: inherit;
}

.link,
button {
  cursor: pointer;
}

.a {
  font-family: "poppins", "Arial", sans-serif;
  font-weight: 400;
  font-style: normal;
}

.b {
  font-family: "poppins", "Arial", sans-serif;
  font-weight: 500;
  font-style: normal;
}

.c {
  font-family: "poppins", "Arial", sans-serif;
  font-weight: 700;
  font-style: normal;
}

.d {
  font-family: "cormorantgaramond", "Arial", sans-serif;
  font-weight: 400;
  font-style: italic;
}

.e {
  font-family: "cormorantgaramond", "Arial", sans-serif;
  font-weight: 600;
  font-style: normal;
}

.f {
  font-family: "cormorantgaramond", "Arial", sans-serif;
  font-weight: 700;
  font-style: normal;
}

.page-navigation ul {
  display: flex;
}
.page-navigation li:not(:last-child) {
  margin-right: 2%;
  padding-right: 10px;
}
.page-navigation .link {
  opacity: 0.7;
}
.page-navigation .link:hover, .page-navigation .link:focus {
  text-decoration: underline;
}
.page-navigation .link:active {
  opacity: 1;
}